<template>
  <Input
    v-model="value"
    class="coin-form-input"
    :class="{
      'coin-form-input__focused': isFocused
    }"
    v-bind="$attrs"
    @focus="onInputFocus"
    @blur="onInputBlur"
  >
    <template #append>
      <CryptoLoader v-bind="loader" />
    </template>
  </Input>
</template>

<script>
import { useModel } from '@/shared/utils/useModel'
import Input from '@/shared/components/UIKit/Input'
import CryptoLoader from '@/shared/components/UIKit/Loader/CryptoLoader'
import { ref } from 'vue'

export default {
  name: 'CoinFormInput',
  inheritAttrs: true,
  components: { Input, CryptoLoader },
  emits: ['update:modelValue'],
  props: {
    loader: {
      type: Object,
      default: () => ({})
    },
    modelValue: {
      type: [Number, String],
      required: true
    }
  },
  setup (props, { emit }) {
    const { value } = useModel(props, emit)

    const isFocused = ref(false)

    const onInputFocus = () => {
      isFocused.value = true
    }
    const onInputBlur = () => {
      isFocused.value = false
    }

    return {
      value,
      isFocused,
      onInputBlur,
      onInputFocus
    }
  }
}
</script>

<style lang="scss">
.coin-form-input {
  height: 60px;
  padding: 8px 14px 6px 20px;

  .input__body__field {
    &-input {
      margin-top: 7px;
    }
  }
  input {
    font-weight: bold;
    font-size: 22px;
    line-height: 26px;
  }

  @media screen and (max-width: map-get($mobileLayout, "MaxWidth")) {
    .input__body__field {
      padding-top: 2px;

      input {
        font-size: 18px;
        line-height: 18px;
      }
    }

  }
}
</style>
