<template>
<form @submit.prevent class="coin-form">
  <h6 class="coin-form__title">
    {{ t('common.enterAmount') }}
  </h6>

  <div class="coin-form__body">
    <CoinFormInput
      v-model="sendCoinAmount"
      type="number"
      class="coin-form__body__input"
      :label="t('homePage.currencyToSend', { currency: sendCoin?.title })"
      :loader="{
        crypto: sendCoin?.key,
        loading: loadingCoins === 'sent'
      }"
    />
    <CoinFormInput
      v-model="receiveCoinAmount"
      type="number"
      class="coin-form__body__input"
      :label="t('homePage.currencyToReceive', { currency: receiveCoin?.title })"
      :loader="{
        crypto: receiveCoin?.key,
        loading: loadingCoins === 'receive'
      }"
    />

    <div class="coin-form__body__info">
      <div class="coin-form__body__info__item">
        <Icon
          class="coin-form__body__info__item-icon"
          name="coins"
        />
        <div class="coin-form__body__info__item-label">
          0.002 BTC
        </div>
      </div>
      <div class="coin-form__body__info__item">
        <Tooltip :text="t('coinForm.clockTooltip')">
          <div class="d-flex align-items-center">
            <Icon
              class="coin-form__body__info__item-icon"
              name="clock"
            />
            <div class="coin-form__body__info__item-label">
              {{ t('common.minute', 10) }}
            </div>
          </div>
        </Tooltip>
      </div>
    </div>
  </div>

  <Button
    button-type="submit"
    class="coin-form__action"
    type="primary"
    @click="$emit('submit')"
  >
    {{ t('common.continue') }}
  </Button>
</form>
</template>

<script>
import { MODULES_NAMES } from '@/shared/common/modules'
import { useI18n } from 'vue-i18n'
import { computed, ref } from 'vue'
import { useGetters, useMutations } from '@/shared/plugins/vuex'
import Icon from '@/shared/components/Icon/Icon'
import Button from '@/shared/components/UIKit/Button'
import Tooltip from '@/shared/components/UIKit/Tooltip'
import CoinFormInput from '@/client/components/Inputs/CoinFormInput'

export default {
  name: 'CoinForm',
  components: {
    Icon,
    Button,
    Tooltip,
    CoinFormInput
  },
  emits: ['submit'],
  setup () {
    const { t } = useI18n()
    const {
      getReceiveCoin: receiveCoin,
      getSendCoin: sendCoin
    } = useGetters(MODULES_NAMES.EXCHANGE)
    const {
      SET_SEND_RECEIVE_AMOUNT,
      SET_SEND_COIN_AMOUNT
    } = useMutations(MODULES_NAMES.EXCHANGE)

    const loadingCoins = ref('')
    const timer = ref()

    const receiveCoinAmount = computed({
      get: () => receiveCoin.value.amount,
      set: val => {
        fakeLoading('sent', val)
        SET_SEND_RECEIVE_AMOUNT(val)
      }
    })
    const sendCoinAmount = computed({
      get: () => sendCoin.value.amount,
      set: val => {
        fakeLoading('receive', val)
        SET_SEND_COIN_AMOUNT(val)
      }
    })

    const fakeLoading = (type, val) => {
      if (timer.value) {
        loadingCoins.value = type
        clearTimeout(timer.value)
        timer.value = null
      }
      if (val) {
        loadingCoins.value = type
        timer.value = setTimeout(() => {
          // loadingCoins[type] = false
        }, 1200)
      } else {
        loadingCoins.value = type
      }
    }

    return {
      t,
      receiveCoinAmount,
      sendCoinAmount,
      loadingCoins,
      receiveCoin,
      sendCoin
    }
  }
}
</script>

