    &__input {
      margin: 12px 0;
    }
    &__info {
      display: flex;
      justify-content: center;

      &__item {
        display: flex;
        align-items: center;
        margin: 13px 13px 30px 13px;

        &-label {
          margin-left: 5px;
          font-size: 12px;
          line-height: 14px;

          color: #000000;
          opacity: 0.3;
        }
      }
    }
  }
  &__action {
    width: 100%;
  }

  @media screen and (max-width: map-get($mobileLayout, "MaxWidth")) {
    &__body {
      &__info__item {
        margin-bottom: 22px;
        margin-top: 8px;
      }
    }
